.movie {
  background-color: white;
  width: 40%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-bottom: 50px;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  padding: 0 20px;
  -webkit-box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3),
    0 5px 12px rgba(133, 133, 133, 0.22);
          box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3),
    0 5px 12px rgba(133, 133, 133, 0.22);
}

.movie_columns {
  width: 30%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.movie_columns:last-child {
  padding: 20px 0;
  width: 60%;
}

.movie h1 {
  font-size: 20px;
  font-weight: 600;
}

.movie .movie_genres {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-bottom: 20px;
}

.movie_genres .movie_genre {
  margin-right: 10px;
  color: #b4b5bd;
}

.movie_stars .movie_star {
  color: #ff0;
}

.movie_stars .movie_star_space {
  color: #ccc;
}

.movie .movie_synopsis {
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  color: #b4b5bd;
  overflow: hidden;
}

.movie .movie_poster {
  max-width: 100%;
  position: relative;
  top: -20px;
  -webkit-box-shadow: -10px 19px 38px rgba(83, 83, 83, 0.3),
    10px 15px 12px rgba(80, 80, 80, 0.22);
          box-shadow: -10px 19px 38px rgba(83, 83, 83, 0.3),
    10px 15px 12px rgba(80, 80, 80, 0.22);
}

@media screen and (min-width: 320px) and (max-width: 667px) {
  .movie {
    width: 100%;
  }
}

@media screen and (min-width: 320px) and (max-width: 667px) and (orientation: portrait) {
  .movie {
    width: 100%;
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .movie_poster {
    top: 0;
    left: 0;
    width: 100%;
  }
  .movie_column {
    width: 100% !important;
  }
}



